import { memo, useState, useEffect } from "react";
import { useLocation } from "react-router-dom";
import "./index.css";
import { Container, Button } from "react-bootstrap";
import shuffle from "lodash/shuffle";
import { MathQuestion } from "../../MathQuestion";
import * as Generator from "../../../utils/generator";
import { ENGLISH, CHINESE } from "../../../utils/generator";

function ExamPage(props) {
	const [isSubmitted, setSubmitted] = useState(false);
	const [mathQuestions, setMathQuestions] = useState([]);

	const location = useLocation();

	const resetQuestions = () => {
		const language = { "/tc": CHINESE, "/en": ENGLISH }[location.pathname];

		setMathQuestions(
			shuffle(
				[...Array(34).keys()].map((e) => {
					const mathQ = Generator[`MathQuestionGeneratorType${e + 1}`].generate(language);
					return {
						...mathQ,
						answers: shuffle(mathQ.answers),
					};
				})
			)
		);
	};

	useEffect(() => {
		resetQuestions();
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [location]);

	return (
		<div id="exam-page" style={{ margin: "2em 0" }}>
			<Container fluid>
				{[...Array(mathQuestions.length).keys()].map((v, i) => (
					<MathQuestion key={i} questionIndex={i} {...mathQuestions[v]} isSubmitted={isSubmitted} />
				))}

				<div id="exam-buttons">
					{isSubmitted ? (
						<Button
							variant="dark"
							onClick={() => {
								resetQuestions();
								setSubmitted(false);
								window.scrollTo({ top: 0, smooth: true });
							}}
						>
							Reset Questions
						</Button>
					) : (
						<Button
							variant="dark"
							onClick={() => {
								setSubmitted(true);
								window.scrollTo({ top: 0, smooth: true });
							}}
						>
							Submit Answers
						</Button>
					)}
				</div>
			</Container>
		</div>
	);
}

const memoComponent = memo(ExamPage, (prev, next) => true);
export default memoComponent;
export { memoComponent as ExamPage };
